var prev = document.querySelector('#prev');
var next = document.querySelector('#next');

function nextSlide() {
  var slider = document.querySelector('.whatcustomersay__slider');
  var num;
  for(var i = 0; i <= slider.children.length; i++) {
    if(slider.children[i].dataset.check == 'check') {
      num = slider.children[i].id[1];
      break;
    }
  }

  if(num == 3) {
    document.getElementById('s3').removeAttribute('checked');
    document.getElementById('s3').removeAttribute('data-check');
    document.getElementById('s1').dataset.check = 'check';
    document.getElementById('s1').checked = true;
  } else {
    var newNum = Number(num) + 1;
    console.log(newNum)
    document.getElementById('s' + num).removeAttribute('checked');
    document.getElementById('s' + num).removeAttribute('data-check');
    document.getElementById('s' + newNum).dataset.check = 'check';
    document.getElementById('s' + newNum).checked = true;
  }
}

function prevSlide() {
  var slider = document.querySelector('.whatcustomersay__slider');
  var num;
  for(var i = 0; i <= slider.children.length; i++) {
    if(slider.children[i].dataset.check == 'check') {
      num = slider.children[i].id[1];
      break;
    }
  }

  if(num == 1) {
    document.getElementById('s1').removeAttribute('checked');
    document.getElementById('s1').removeAttribute('data-check');
    document.getElementById('s3').dataset.check = 'check';
    document.getElementById('s3').checked = true;
  } else {
    var newNum = Number(num) - 1;
    console.log(newNum)
    document.getElementById('s' + num).removeAttribute('checked');
    document.getElementById('s' + num).removeAttribute('data-check');
    document.getElementById('s' + newNum).dataset.check = 'check';
    document.getElementById('s' + newNum).checked = true;
  }
}

next.addEventListener('click', nextSlide);
prev.addEventListener('click', prevSlide);

